import { createRouter, createWebHashHistory } from 'vue-router'
import api from "@/model/api.js"
import store from "@/store"
const routes = [
    {
      path: '/',
      name: 'LayoutPage',
      component: ()=>import("@/pages/LayoutPage.vue"),
      redirect:"/Login",
      meta: {
        requireAuth: false
      },
      children:[
        {
          name:"Welcome",
          path:"/Welcome",
          component: ()=>import("@/pages/WelcomePage.vue"),
          meta: {
            requireAuth: false,
            name:"Chào mừng",
          },
        },
        {
          name:"Login",
          path:"/Login",
          component: ()=>import("@/components/LoginPage.vue"),
          meta: {
            requireAuth: false ,
            name:"Đăng nhập",
          },
        },
        {
          name:"TestPage",
          path:"/TestPage",
          component: ()=>import("@/components/TestPage.vue"),
          meta: {
            requireAuth: false
          },
        },

        {
          name:"ImportedGoods",
          path:"/ImportedGoods",
          component: ()=>import("@/components/ManageImportedGoods/ImportedGoods.vue"),
          meta: {
            requireAuth: false ,
            name:"Hàng nhập",
          },
        },
        {
          name:"ProductDelivery",
          path:"/ProductDelivery",
          component: ()=>import("@/components/ManageExportGoods/ProductDelivery.vue"),
          meta: {
            requireAuth: false ,
            name:"Xuất hàng",
          },
        },
        {
          name:"ContactList",
          path:"/ContactList",
          component: ()=>import("@/components/ManageSystem/ContactList.vue"),
          meta: {
            requireAuth: false ,
            name:"Liên hệ",
          },
        },
        {
          name:"NewApplicationForIssuance",
          path:"/NewApplicationForIssuance",
          component: ()=>import("@/components/ApplicationForIssuance/NewApplicationForIssuance.vue"),
          meta: {
            requireAuth: false ,
            name:"Đơn xin cấp phát",
          },
        },
        {
          name:"GoodsInfor",
          path:"/GoodsInfor",
          component: ()=>import("@/components/ManageSystem/GoodsInfor.vue"),
          meta: {
            requireAuth: false ,
            name:"Thông tin các loại vật phẩm",
          },
        },
        {
          name:"SearchAppForIssuance",
          path:"/SearchAppForIssuance",
          component: ()=>import("@/components/ApplicationForIssuance/SearchAppForIssuance.vue"),
          meta: {
            requireAuth: false ,
            name:"Tra cứu đơn xin cấp phát",
          },
        },
        
        {
          name:"HelloWorld",
          path:"/HelloWorld",
          component: ()=>import("@/components/ManageExportGoods/HelloWorld.vue"),
          meta: {
            requireAuth: false ,
            name:"Tra cứu đơn xin cấp phát",
          },
        },
        
      ],
    },
  ]
  const router = createRouter({
    history: createWebHashHistory()  , // createWebHistory(process.env.BASE_URL),
    routes
  })
// de deploy nen IIS :  khi nguoi dung enter link truc tiep tren Address bar 
// se bao loi not found or error khac, de tranh loi nay ta can su dung createWebHashHistory()
//createWebHashHistory() (hash mode): la che do cho phep cac link dieu huong noi bo tren
// trinh duyet nguoi dung ma ko can gui request len server check lai
// khi su dung createWebHashHistory() , thi this.$router.push phai su dung them thuoc tinh hash:"hash"
// vd:   this.$router.push({path:"/HelloWorld",hash:"hash",  query:{name1:"dadad"}})
  router.beforeEach((to, from, next)=>{
    if ( to.name !== 'Login' && to.meta.requireAuth==true
     ){
      api.CheckSession((dat)=>{
          // current emp infor
          store.commit("setEmpInfor", dat.data.data);
          next();
        },(er)=>{
          // session timeout or not login
          if(er?.response?.status == 401)
          {
            console.error(er.response.data);
            next({
              name: 'Login',
              params:{}
            })
          }
        })

    } 
    else {
      next();
    }
  })

  export default router